.interface-complementary-area__pin-unpin-item {
  display: none !important;
}

.interface-complementary-area-header .components-button.has-icon {
  padding: 6px 13px;
}

.content-management .content-management__panel {
  position: relative;
}
.content-management .content-management__panel.is-opened {
  padding: 0;
}
.content-management .content-management__panel.is-opened .components-panel__body-title {
  margin: 0;
}
.content-management .content-management__panel.is-renaming .content-management__panel__row:not(.is-renaming),
.content-management .content-management__panel.is-renaming .content-management__add-new-button {
  opacity: 0.5;
  pointer-events: none;
}
.content-management .content-management__panel.is-renaming .content-management__panel__actions__button {
  display: none !important;
}
.content-management .content-management__panel .content-management__panel__row {
  border-left: 3px solid transparent;
  margin-top: 0;
  position: relative;
}
.content-management .content-management__panel .content-management__panel__row.current {
  background-color: rgba(245, 245, 245, 0.5);
  border-left-color: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__row.current .content-management__panel__actions__button {
  opacity: 100;
}
.content-management .content-management__panel .content-management__panel__row.is-renaming {
  background-color: #e8f7f7;
  color: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__row.is-renaming .content-management__panel__button > input {
  color: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__row:not(.is-renaming):hover, .content-management .content-management__panel .content-management__panel__row:not(.is-renaming) button:focus {
  background-color: rgba(245, 245, 245, 0.75);
}
.content-management .content-management__panel .content-management__panel__row:not(.is-renaming):hover .content-management__panel__actions__button,
.content-management .content-management__panel .content-management__panel__row:not(.is-renaming):hover + .content-management__panel__actions .content-management__panel__actions__button, .content-management .content-management__panel .content-management__panel__row:not(.is-renaming) button:focus .content-management__panel__actions__button,
.content-management .content-management__panel .content-management__panel__row:not(.is-renaming) button:focus + .content-management__panel__actions .content-management__panel__actions__button {
  opacity: 100;
}
.content-management .content-management__panel .content-management__panel__row .content-management__panel__actions .content-management__panel__actions__button:focus,
.content-management .content-management__panel .content-management__panel__row .content-management__panel__actions .content-management__panel__actions__button.is-open {
  opacity: 100;
}
.content-management .content-management__panel .content-management__panel__row:last-of-type {
  margin-bottom: 15px;
}
.content-management .content-management__panel .content-management__panel__button.is-link {
  border: 0;
  box-shadow: none;
  color: #1e1e1e !important;
  display: flex;
  font-size: 14px;
  font-weight: 400;
  padding: 9px 10px;
  text-decoration: none;
  transition: border-left-color 0.2s;
  width: 100%;
}
.content-management .content-management__panel .content-management__panel__button.is-link:active:not(:disabled), .content-management .content-management__panel .content-management__panel__button.is-link:hover:not(:disabled) {
  color: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__button.is-link > svg {
  flex: 0 0 24px;
  margin-right: 6px;
}
.content-management .content-management__panel .content-management__panel__button.is-link > span {
  display: inline-block;
  flex-grow: 1;
  padding-right: 56px;
}
.content-management .content-management__panel .content-management__panel__button.is-link > span[contenteditable=true] {
  color: var(--wp-admin-theme-color);
  font-weight: 500;
}
.content-management .content-management__panel .content-management__panel__button.is-link > span[contenteditable=true]:hover {
  cursor: text;
}
.content-management .content-management__panel .content-management__panel__button.is-link > span:focus-visible {
  outline: 0;
}
.content-management .content-management__panel .content-management__panel__actions__button:hover svg {
  fill: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__actions {
  position: absolute;
  right: 8px;
  top: 4px;
}
.content-management .content-management__panel .content-management__panel__actions__button {
  opacity: 0;
}
.content-management .content-management__panel .content-management__panel__actions__button.is-open {
  display: block;
}
.content-management .content-management__panel .content-management__panel__actions__button .components-dropdown-menu__toggle:hover > svg > path {
  fill: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__rename-actions {
  position: absolute;
  right: 9px;
}
.content-management .content-management__panel .content-management__panel__rename-action {
  justify-content: center;
  min-width: 36px;
  padding: 5px;
}
.content-management .content-management__panel .content-management__panel__rename-action--confirm {
  color: var(--wp-admin-theme-color);
}
.content-management .content-management__panel .content-management__panel__rename-action--cancel {
  color: var(--reliable-red-400);
}
.content-management .content-management__panel .content-management__panel__separator {
  color: #949494;
  font-weight: 300;
  margin: 0;
  padding: 10px 18px;
  position: relative;
}
.content-management .content-management__panel .content-management__panel__separator::before {
  background-color: #f0f0f0;
  content: "";
  display: block;
  height: 1px;
  left: 18px;
  position: absolute;
  right: 18px;
  top: 50%;
}
.content-management .content-management__panel .content-management__panel__separator > span {
  background-color: #fff;
  display: inline-block;
  padding-right: 10px;
  position: relative;
}
.content-management .content-management__add-new-button {
  position: absolute;
  right: 40px;
  top: 6px;
}
.content-management .content-management__add-new-button:hover, .content-management .content-management__add-new-button:focus {
  cursor: pointer;
}
.content-management .content-management__add-new-button:hover span, .content-management .content-management__add-new-button:focus span {
  background-color: var(--wp-admin-theme-color);
}
.content-management .content-management__add-new-button:hover svg, .content-management .content-management__add-new-button:focus svg {
  color: var(--wp-admin-theme-color-darker-10);
}
.content-management .content-management__add-new-button svg {
  fill: var(--wp-admin-theme-color);
}

.components-popover__content .content-management-actions__item:focus {
  color: var(--wp-admin-theme-color) !important;
}

.components-button.is-destructive:active:not(:disabled) {
  background: unset;
}

.content-management-actions__item.is-destructive {
  box-shadow: none !important;
  color: var(--reliable-red-400) !important;
}
.content-management-actions__item.is-destructive:hover > svg > path,
.content-management-actions__item.is-destructive > svg > path {
  fill: var(--reliable-red-400);
}
.content-management-actions__item.is-confirming {
  color: var(--wp-admin-theme-color);
}
.content-management-actions__item.is-confirming:hover > svg > path,
.content-management-actions__item.is-confirming > svg > path {
  fill: var(--wp-admin-theme-color);
}
.content-management-actions__item svg {
  display: inline-block;
  margin-right: 0.75em;
  width: 20px;
}
.content-management-actions__item:hover, .content-management-actions__item:focus:not(:disabled) {
  box-shadow: none !important;
  outline: 0 !important;
}
.content-management-actions__item:hover > svg > path {
  fill: var(--wp-admin-theme-color);
}
.content-management-actions .components-popover__content {
  border-color: #1e1e1e;
  border-radius: 2px;
  margin-right: -18px !important;
  margin-top: -10px;
  min-width: 190px;
  padding: 5px 0;
}
.content-management-actions .components-popover__content > div {
  padding: 0 !important;
}
.content-management-actions .components-popover__content .components-menu-item__item {
