  padding-right: 45px;
  text-align: left;
  width: 100%;
}
.content-management-actions .components-popover__content .content-management-actions__item {
  padding: 12px 20px;
}
.content-management-actions .components-popover__content .content-management-actions__item svg {
  float: right;
  margin: -4px -45px 0 0;
}